import React, { Component } from "react";
import Link from "gatsby-link";

class DocsPolicy extends Component {
    render() {
        const data = this.props.data;
        return (
            <div>
                <div className="container-fluid">
                    <div className="sidenav-doc">
                        <Link to="docs/">Policy</Link>
                        <Link to="docs/skills">Skills</Link>
                        <Link to="docs/spells">Spells</Link>
                        <Link to="docs/powers">Powers</Link>
                        <a href="#section3"></a>
                    </div>
                </div>
                <div className="document">
                    <h1 className="text-center">Policies</h1>
                    {data.allMarkdownRemark.edges.map(({ node }) =>
                        <div dangerouslySetInnerHTML={{__html: node.html}} />
                    )}
                </div>    
            </div>
        );
    }
}

export default DocsPolicy;
    

export const query = graphql `
query Skills {
    allMarkdownRemark (
        filter: {fileAbsolutePath: {regex: "/(docs)/(policy)/.*\\.md$/"}}
    ) {
        edges {
            node {
                id
                html
            }
        }
    }
}
`


